<template>
  <div class="searchs">
    <el-form :inline="true">
     
      <el-form-item label="年份:">
        <el-select v-model="region" placeholder="所有年份">
            <el-option
              v-for="item in allyears"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
           </el-select>
        </el-form-item>
        <el-form-item label="行政区划:">
        <el-select v-model="region1" placeholder="全国">
            <el-option
              v-for="item in divisions"
              :key="item.xzqdm"
              :label="item.xzqmc"
              :value="item.xzqdm">
            </el-option>
           </el-select>
        </el-form-item>
          <el-form-item label="项目类型:">
            <el-cascader
              v-model="region2"
              :options="types2"
              :props="{ checkStrictly: true }"
              placeholder="全部类型"
              ></el-cascader>
        <!-- <el-select v-model="region2" placeholder="全部类型">
            <el-option
              v-for="item in types"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
           </el-select> -->
        </el-form-item>
        <el-form-item label="项目名称:">
          <el-input v-model="proName"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit(false)">查询</el-button>
          <el-button @click="onReset">重置</el-button>
          <el-button @click="output">导出</el-button>
          <el-button @click="avdafilter">高级筛选</el-button>
          <!-- <el-button type="primary" @click="onSubmitxs(0)" v-if="sekey==3">按项目统计</el-button>
          <el-button type="primary" @click="onSubmitxs(1)" v-if="sekey==3">按省份统计</el-button> -->
        </el-form-item>
         <el-form-item v-if="sekey==4">
           <el-button type="primary" icon="el-icon-setting" @click="setcul">设置</el-button>
        </el-form-item>
    </el-form>
   
    </div>
  </div>
</template>

<script>
// import {getxzqlist,getxmlxlist,xmdowln,zhsjtjdowln,sfdowln,exportXmjbxxtj,exportJctbxxtj,nfList} from '@/api/pgsj';
// import {down} from '../../../util/utils'
// import EventBus from '../../../util/eventbus'
export default {
    data () {
        return {
          proName:'',
          region: "",
          region1: "",
          region2: "全部类型",
          isreset: false,
          allyears: [
            {
              value: '所有年份',
              label: '所有年份'
            },
           
            // {
            //   value: '2020',
            //   label: '2020'
            // },
            // {
            //   value: '2019',
            //   label: '2019'
            // },
          ],
          divisions:[
          ],
          types:[
             {
              value: '全部类型',
              label: '全部类型'
            },
            {
              value: '机场',
              label: '机场'
            },
            {
              value: '铁路',
              label: '铁路'
            },
          ],
          types2:[
              ],
          isxm:true,
        };
    },
    props:[
      "sekey"
    ],
    mounted () {
      // let that = this
      // nfList().then(res=>{
      //   res.data.data.forEach(element => {
      //     this.allyears.push({
      //       name:element,
      //       value:element
      //     })
      //   });
      // })
      // this.init()
      // EventBus.$on("tjoptioned",()=>{
      //   that.onReset()
      // })
    },
    methods: {
      onSubmit(val){
        
        this.isreset=val
        // console.log(this.region2,'！！！！！！')
        this.$emit("sparams",{region:this.region,region1:this.region1,region2:this.region2,isreset:false,proName:this.proName})
      },
      onReset(){
        this.isreset=true
        this.proName = ''
        this.region1=""
        this.region=""
        this.region2=[]
        window.vm.$emit('isreset', true);
        this.$emit("sparams",{region:"",region1:"",region2:"[]",isreset:true,proName:this.proName})
        // this.$emit("sparams",{region:"所有年份",region1:"全国",region2:"全部类型",isreset:true})
      },
      avdafilter(){
        this.$emit("avdas")
      },
      output(){
        //   const loading = this.$loading({
        //   lock: true,
        //   text: 'Loading',
        //   spinner: 'el-icon-loading',
        //   background: 'rgba(0, 0, 0, 0.7)'
        // });
        let params={
          ejl:"",
          nd: 2021,
          xzqdm:""
        }
        if(this.region1!=""){
          if(this.region1!="全国"){
            params.xzqdm=this.region1
          }
        }
         if(this.region2!=""){
          if(this.region2!="全部类型"){
            console.log(this.region2,'第二个选项！！！！')
            params.yjl =  this.region2[0]
            params.ejl = this.region2[1]
          }
        }
        if(this.isxm){
              if(this.sekey==3){
                  xmdowln(params).then(res=>{
                    down(res.data,this.region1)
                    loading.close()
                  }).catch(err=>{
                    loading.close()
                  })
                }
                if(this.sekey==1){
                    exportXmjbxxtj(params).then(res=>{
                    down(res.data,this.region1)
                    loading.close()
                  }).catch(err=>{
                    loading.close()
                  })
                }
              if(this.sekey==2){
                exportJctbxxtj(params).then(res=>{
                    down(res.data,this.region1)
                    loading.close()
                  }).catch(err=>{
                    loading.close()
                  })
                }
              if(this.sekey==4){
                  zhsjtjdowln(params).then(res=>{
                    down(res.data,this.region1)
                    loading.close()
                  }).catch(err=>{
                    loading.close()
                  })
                }
        }else {
          sfdowln(params).then(res=>{
            down(res.data,this.region1)
            loading.close()
          }).catch(err=>{
            loading.close()
          })
        }
      },
      init(){
        this.getxzq();
        this.getxmlx();
      },
      getxzq(){
      
              getxzqlist().then(res=>{
                this.divisions=res.data.data
                 this.divisions.unshift({"xzqdm":"全国","xzqmc":"全国","sjxzqdm":"","sjxzqmc":""})
              })
             
      },
      getxmlx(){
        
       
              getxmlxlist().then(res=>{
                this.types2=res.data.data
                this.types2.unshift({"label":"全部类型","value":""})
              })
      },
      onSubmitxs(val){
        if(val==1){
           window.vm.$emit('isxm', false);
           this.isxm=false
        }else{
          window.vm.$emit('isxm', true);
          this.isxm=true
        }
       
      },
      setcul(){
        this.$emit('setcul');
      }
    }
};
</script>

<style lang="scss" scoped>
.search{
  display: inline-block;
}
</style>



