            <el-button type="primary" size="mini" @click="qhesw">切换二三维</el-button>
             <el-button type="primary" size="mini" @click="tjd">添加点</el-button>
              <el-button type="primary" size="mini" @click="tjx">添加线</el-button>
               <el-button type="primary" size="mini" @click="tjm">添加面</el-button>
          </div>
      
          <!-- <img src="../assets/images/bj.png" alt=""> -->
          <!-- <div>
            <el-checkbox v-model="checked"  @change="handleCheckAllChange">星一</el-checkbox>

          </div>
