</template>

<script>
// import { Math as CMath, CallbackProperty } from "cesium";
import getCzml from '../assets/utils/GetCzml';
import AddCzmlDataSource from '../assets/utils/AddCzmlDataSource';
export default {
  name: 'HelloWorld',
  data () {
    return {
		satelliteEntities:new Map(),
        checked:false,
        checked1:false,
        checked2:false,
        viewer:null,
        checked9:false,
        qh:true,
        checked10:false
    }
  },
  mounted () {
	  let that = this;
    this.$nextTick(() => {
		var viewer;
		                viewer = new Cesium.Viewer('cesiumContainer', {
							// imageryProvider: new Cesium.SingleTileImageryProvider({
							//             url: Cesium.buildModuleUrl('Assets/Textures/earth_color_low_4096.jpg')
							//         }),
		                    animation: true,
		                    timeline: false,
		                    baseLayerPicker: false,
		                    geocoder: false,
		                    sceneModePicker: false,
		                    navigationHelpButton: false
		                });
                        that.viewer=viewer;
						viewer.imageryLayers.removeAll();

						viewer.scene.globe.depthTestAgainstTerrain = true;
						viewer._cesiumWidget._creditContainer.style.display = "none";
			var tdtLayer = new Cesium.UrlTemplateImageryProvider({
                url: "http://webrd02.is.autonavi.com/appmaptile?lang=zh_cn&size=1&scale=1&style=8&x={x}&y={y}&z={z}",
                minimumLevel: 3,
                maximumLevel: 18
            })
            viewer.imageryLayers.addImageryProvider(tdtLayer);
			//高德影像
			             tdtLayer = new Cesium.UrlTemplateImageryProvider({
			                url: "https://webst02.is.autonavi.com/appmaptile?style=6&x={x}&y={y}&z={z}",
			                minimumLevel: 3,
			                maximumLevel: 18
			            })
			            viewer.imageryLayers.addImageryProvider(tdtLayer);
		 //高德路网中文注记
		            tdtLayer = new Cesium.UrlTemplateImageryProvider({
		                url: "http://webst02.is.autonavi.com/appmaptile?x={x}&y={y}&z={z}&lang=zh_cn&size=1&scale=1&style=8",
		                minimumLevel: 3,
		                maximumLevel: 18
		            })

		            viewer.imageryLayers.addImageryProvider(tdtLayer);
                viewer.clock.shouldAnimate=true;
                document.getElementsByClassName('cesium-viewer-animationContainer')[0].style.display="none";
					that.addPoint();

          that.addtree();
          that.gohome();
				
	});
	},
	methods:{
    qhesw(){
      if(this.qh){
          //二维
        this.viewer.scene.morphTo2D(1);//二维
      }else{
            //三维

        this.viewer.scene.morphTo3D(1);//三维
      }
        
        this.qh=!this.qh
    },
        chx(){
            this.checked9=!this.checked9;
            this.viewer.scene.globe.enableLighting=this.checked9;
        },
        dqq(){
            this.checked10=!this.checked10;
            this.viewer.scene.globe.showGroundAtmosphere = this.checked10;
        },
        handleCheckAllChange(){
            if(this.checked){
                this.addEntity(this.viewer,1)

            }else{
                this.viewer.dataSources.remove(this.satelliteEntities.get(1))
                this.viewer.entities.remove(this.satelliteEntities.get(6))
                this.satelliteEntities.delete(1)
                this.satelliteEntities.delete(6)
                
            }
        },
        handleCheckAllChange1(){
            if(this.checked1){
                this.addEntity(this.viewer,2)

            }else{
                this.viewer.dataSources.remove(this.satelliteEntities.get(2))
                this.satelliteEntities.delete(this.satelliteEntities.get(2))
            }
},
handleCheckAllChange2(){
    if(this.checked2){
    this.addEntity(this.viewer,3)
    }
    else{
        this.viewer.dataSources.remove(this.satelliteEntities.get(3))
        this.satelliteEntities.delete(this.satelliteEntities.get(3))
    }
},
		addEntity(viewer,code){
		let data = null;
        let that=this;
        if(this.checked){
                    data =    {
                "code": "xyz",
                "index": 0,
                "tle1": "1 50000U 18001B   21349.00000000  .00000000  00000-0  30000-3 0   111",
                "tle2": "2 50000  99.4085  63.8633 0004624 104.1156 214.7760 15.25669381  2222",
                "name": "星一",
                "startTime": "2021-12-15 17:50:00"
                }
        }
        	
        if(this.checked1){
                    data =    {
                "code": "xyz",
                "index": 0,
                "tle1": "1 50000U 18001B   21349.00000000  .00000000  00000-0  30000-3 0   111",
                "tle2": "2 50000  80.4085  63.8633 0004624 104.1156 214.7760 15.25669381  2222",
                "name": "星二",
                "startTime": "2021-12-15 17:50:00"
                }
        }
         if(this.checked2){
                    data =    {
                "code": "xyz",
                "index": 0,
                "tle1": "1 50000U 18001B   21349.00000000  .00000000  00000-0  30000-3 0   111",
                "tle2": "2 50000  97.4085  63.8633 0004624 104.1156 214.7760 15.25669381  2222",
                "name": "星三",
                "startTime": "2021-12-15 17:50:00"
                }
        }

			let pathColor = "#ff0000";
        pathColor = window.Cesium.Color.fromCssColorString(pathColor);
        // 中继卫星字体颜色
        let labelColor = "#ffff00";
        labelColor = window.Cesium.Color.fromCssColorString(labelColor);
        // this.configParams.modelScale = 200;
        let time = new Date(data.startTime);
        const option = {
            text: data.name,
            startTime: time,
            //endTime: new Date(time.getTime() + 1000 * 60 * 60 * 24),
            BoardUri: './static/images/satellite-yellow1.png',
            // billboardScale: 1, //卫星图表缩放系数 0 ～ 1
            BoardShow: false,
            font:  "5px sans-serif", // 卫星名称字体和字号大小
            pathColor: [pathColor.red * 255, pathColor.green * 255, pathColor.blue * 255, 255],
            pathWidth:  1,
            // model: './static/data/beidou.gltf',
            // model: {uri:'http://192.168.1.31:8080/wxts54/static/data/beidou.gltf'},
            // model: gltf,
            modelScale: 500,
            labelColor: [labelColor.red * 255, labelColor.green * 255, labelColor.blue * 255, 255],
            stepNum: 180, //卫星步长
            time: 24
        };
        const tle = {
            tle1: data.tle1,
            tle2: data.tle2,
        };
        // 生成卫星轨道 CZML 数据
        const czml = getCzml(tle, option);
		// console.log("添加卫星：",czml)

        if (czml) {
            const responseData = AddCzmlDataSource(viewer, czml);
            responseData.then(entity => {
                // entity.entities.values[0].code = code;
                //判断中继卫星配置的是图片还是模型
                // if (this.configParams.relationRelaySatelliteType == 'model') {
                //     entity.entities.values[0].billboard.show = false;
                //     entity.entities.values[0].model.show = true;
                // } else {
                //     entity.entities.values[0].billboard.show = true;
                //     entity.entities.values[0].model.show = false;
                // }

                entity.entities.values[0].info = {
                    type: "relay",
                    name: data.name,
                    // code: code,
                    iswx: true,
                    fy: 0,
                    ph: 0,
                    gd: 0
                };
                // 中继目标实体列表
                entity.info = {
                    type: "relay",
                    name: data.name,
                    // code: code,
                    iswx: true,
                    fy: 0,
                    ph: 0,
                    gd: 0
                };
                // entity.id = code;
                entity.show = true;
                // entity.entities.values[0].path.show = !(this.satelliteParameters['隐藏轨道'] || this.satelliteParameters['隐藏轨道及弧段'])

                entity.entities.values[0].model.minimumPixelSize = 32;
                entity.entities.values[0].model.distanceDisplayCondition = new window.Cesium.DistanceDisplayCondition(
                    0,
                    10000000
                );
                entity.entities.values[0].billboard.distanceDisplayCondition = new window.Cesium.DistanceDisplayCondition(
                    10000000,
                    Number.MAX_VALUE
                );
                // entity.entities.values[0].model.uri = gltf;
                // entity.entities.values[0].model.show = true;
                // entity.entities.values[0].billboard.show = true;
                // entity.entities.values[0].label.distanceDisplayCondition = new window.Cesium.DistanceDisplayCondition(
                //     0,
                //     0
                //   );
                // if (!this.nowWXIcon) {
                //     entity.entities.values[0].model.show = false;
                //     entity.entities.values[0].billboard.show = false;
                // }

                // 区分存储实体数据
                // endTime === 24 ?
                //     this.satelliteEntities.set(code, entity) :
                //     this.arcSegmentEntities.set(code, entity);
                // if (code == 6) {
                //     //  this.startTime = new Date('2023-04-12T14:23:53.3246000001890934Z').getTime()
                //     entity.startTime = new Date('2021-12-15T05:49:20Z').getTime()
                //     entity.endTime = new Date('2021-12-15T05:49:30Z').getTime()
                // }
                // if (code == 7) {
                //     //  this.startTime = new Date('2023-04-12T14:23:53.3246000001890934Z').getTime()
                //     entity.startTime = new Date('2021-12-15T05:50:00Z').getTime()
                //     entity.endTime = new Date('2021-12-15T05:50:10Z').getTime()modelScale
                // }
                // this.satelliteEntities.set(code, entity)

                this.satelliteEntities.set(code, entity)

              
                // EventBus.$emit("getWxEntities", { entity: sate, type: "wx", lbtype: "wxlb", configParams: this.configParams, data: data });
                // window.parent.postMessage({ entity: this.satelliteEntities, type: "wx", lbtype: "wxlb" }, "*");
                // setTimeout(() => {
                //     viewer.clock.startTime = Cesium.JulianDate.fromDate(new Date("2023-03-01 08:00:00"));//开始时间
                //     viewer.clock.stopTime = Cesium.JulianDate.fromDate(new Date("2023-03-01 24:00:00"));//结束时间
                //     viewer.clock.currentTime = Cesium.JulianDate.fromDate(new Date("2023-03-01 08:00:00"));//当前时间
                //     viewer.timeline.zoomTo(viewer.clock.startTime, viewer.clock.stopTime);
                // }, 2000)
                if(that.checked){
                    if(!this.satelliteEntities.get(6)){
                        that.AddLink(entity,that.viewer,35,45,true)
                    }
                }
            });
		}
	},
    gohome(){
        this.viewer.scene.camera.flyTo({
        destination: Cesium.Cartesian3.fromDegrees(106.0, 38, 12199999),
        orientation: {
          heading: Cesium.Math.toRadians(348),
          pitch: Cesium.Math.toRadians(-90),
          roll: 0.0,
        },
        duration: 2,
      });
    },
    addPoint(){
        this.viewer.entities.add({
    // https://cesium.com/docs/cesiumjs-ref-doc/Entity.html
    // name: options?.name,
    // targetType: options ?.type,
    // description: options ?.description,
    position: window.Cesium.Cartesian3.fromDegrees(35, 45, 1000),
    // https://cesium.com/docs/cesiumjs-ref-doc/LabelGraphics.html#.ConstructorOptions
    // label: {
    //   text: options?.text,
    //   fillColor: options?.labelColor ? Color.fromCssColorString(options.labelColor) : Color.YELLOW,
    //   font: "12px Microsoft YaHei",
    //   pixelOffset: new Cartesian2(15, 0),
    //   horizontalOrigin: HorizontalOrigin.LEFT,
    //   verticalOrigin: VerticalOrigin.CENTER,
    // },
    label: {
      show: true,
      text: "一点",
      // areaDiaphaneity
      fillColor:Cesium.Color.YELLOW,
      font: "12px Microsoft YaHei",
      verticalOrigin: 6,
      eyeOffset: new Cesium.Cartesian3(0, 0, -50000)
      // disableDepthTestDistance: Number.POSITIVE_INFINITY
    },
    // https://cesium.com/docs/cesiumjs-ref-doc/PointGraphics.html#.ConstructorOptions
    point: {
      pixelSize:  5,
      color:  window.Cesium.Color.RED,
    //   distanceDisplayCondition: mapConfig.maxDistance,
      // heightReference:Cesium.HeightReference.CLAMP_TO_GROUND,
    //   disableDepthTestDistance: Number.MAX_VALUE
    },
  });
    },
    tjd(){
      let entity =  this.viewer.entities.add({
    // https://cesium.com/docs/cesiumjs-ref-doc/Entity.html
    // name: options?.name,
    // targetType: options ?.type,
    // description: options ?.description,
    position: window.Cesium.Cartesian3.fromDegrees(35, 45, 1000),
    // https://cesium.com/docs/cesiumjs-ref-doc/LabelGraphics.html#.ConstructorOptions
    // label: {
    //   text: options?.text,
    //   fillColor: options?.labelColor ? Color.fromCssColorString(options.labelColor) : Color.YELLOW,
    //   font: "12px Microsoft YaHei",
    //   pixelOffset: new Cartesian2(15, 0),
    //   horizontalOrigin: HorizontalOrigin.LEFT,
    //   verticalOrigin: VerticalOrigin.CENTER,
    // },
    label: {
      show: true,
      text: "一点",
      // areaDiaphaneity
      fillColor:Cesium.Color.YELLOW,
      font: "12px Microsoft YaHei",
      verticalOrigin: 6,
      eyeOffset: new Cesium.Cartesian3(0, 0, -50000)
      // disableDepthTestDistance: Number.POSITIVE_INFINITY
    },
    // https://cesium.com/docs/cesiumjs-ref-doc/PointGraphics.html#.ConstructorOptions
    point: {
      pixelSize:  5,
      color:  window.Cesium.Color.RED,
    //   distanceDisplayCondition: mapConfig.maxDistance,
      // heightReference:Cesium.HeightReference.CLAMP_TO_GROUND,
    //   disableDepthTestDistance: Number.MAX_VALUE
    },
  });
  this.viewer.flyTo(entity);
    },
    tjx(){

    },
    tjm(){

    },
    AddLink(startEntity, viewer, stationLng, staionLat, isBL) {
  var lat = 87.501716;
  var lng = 44.000771;
  var height = 2500000;
  let entity=null;
//   var timer = setInterval(() => {
//     var cutTime = viewer.clock.currentTime;
//     let epos = startEntity.entities.values[0].position.getValue(cutTime);
//     if (!epos) {
//       return
//     }
//     // console.log(epos);
//     var cartographic = viewer.scene.globe.ellipsoid.cartesianToCartographic(epos);
//     lat = CMath.toDegrees(cartographic.latitude); //纬度
//     lng = CMath.toDegrees(cartographic.longitude); //经度
//     height = cartographic.height;
//   }, 500);
  let line={
    polyline: {
      positions: new Cesium.CallbackProperty(function () {
        var cutTime = viewer.clock.currentTime;
        let epos = startEntity.entities.values[0].position.getValue(cutTime);
        if (!epos) {
          return
        }
        // console.log(epos);
        var cartographic = viewer.scene.globe.ellipsoid.cartesianToCartographic(epos);
        lat = Cesium.Math.toDegrees(cartographic.latitude); //纬度
        lng = Cesium.Math.toDegrees(cartographic.longitude); //经度
        height = cartographic.height;
        
        return Cesium.Cartesian3.fromDegreesArrayHeights([lng, lat, height, stationLng, staionLat, 0]);
        // taskType === "对地数传"
        //   return /数传/.test(taskType)
        //     ? window.Cesium.Cartesian3.fromDegreesArray([lng, lat, stationLng, staionLat])
        //     : window.Cesium.Cartesian3.fromDegreesArray([stationLng, staionLat, lng, lat]);
        // }, false),
        // return /数传/.test(taskType)
        //   ? window.Cesium.Cartesian3.fromDegreesArrayHeights([lng, lat, height, stationLng, staionLat, 0])
        //   : window.Cesium.Cartesian3.fromDegreesArrayHeights([stationLng, staionLat, 0, lng, lat, height]);
      }, false),
      width: 2,
      // followSurface: false,
      // material: new window.window.Cesium.PolylineAttackLinkMaterialProperty({
    //   material:
    //     !isBL
    //       ? new Cesium.PolylineTrailLinkMaterialProperty(
    //         Cesium.Color.fromCssColorString("#0055ff"),
    //         1000
    //       )
    //       : new Cesium.PolylineBLLinkMaterialProperty(
    //        Cesium.Color.fromCssColorString("#0055ff"),
    //         1000
    //       ),
      // clampToGround: true,
    //   arcType: Cesium.ArcType.NONE,
    }
  };
  let entity1=viewer.entities.add(line)
  this.satelliteEntities.set(6, entity1)
},
addtree(){
  let that=this;
   layui.use('tree', function(){
    var tree = layui.tree;
   
    //渲染
    var inst1 = tree.render({
      elem: '#test1'  //绑定元素
      ,showCheckbox: true,  // 是否显示复选框
      id:'demo-id-1',
      data: [{
    title: '上星列表',
    num:100
    ,id: 1
    ,children: [{
      title: '星1',
      num:10,
      id: 1000
      // ,children: [{
      //   title: 'xx'
      //   ,id: 10001
      // },{
      //   title: 'xx'
      //   ,id: 10002
      // }]
    },{
      title: '星2',
      num:50
      ,id: 1002,
      // children: [{
      //   title: 'xx'
      //   ,id: 1000001
      // },{
      //   title: 'xx'
      //   ,id: 1000002
      // }]
    }]
  },{
      title: '下星列表',
      num:90
      ,id: 11,children: [{
        title: '星3',
        num:80
        ,id: 100001,
      //   children:[
      //   {
      //   title: 'xxx'
      //   ,id: 100002
      // }
      //   ]
      }]
    }],
  onlyIconControl: true,
  mdefinend:[{class:'myinfo',title:"描述"},{class:'myinfo2',title:"下载"}],
  click: function(obj){
       layer.msg(JSON.stringify(obj.data));
      
    },
    onClick:function(obj){
      layer.msg(JSON.stringify(obj));
    },
    oncheck:function(obj){
      console.log(JSON.stringify(obj));
      if(obj.data.title=="星1"){
        that.checked=obj.checked;
        
that.handleCheckAllChange()
      }
       if(obj.data.title=="星2"){
        that.checked1=obj.checked;
        
that.handleCheckAllChange1()
      }
      if(obj.data.title=="星3"){
        that.checked2=obj.checked;
        
that.handleCheckAllChange2()
      }

         if(obj.data.title=="上星列表"){
        that.checked=obj.checked;
that.handleCheckAllChange()
setTimeout(function(){
        that.checked1=obj.checked;

  that.handleCheckAllChange1()
},500)

      }
       if(obj.data.title=="下星列表"){
        that.checked2=obj.checked;
        
that.handleCheckAllChange2()
      }
    }
    });
  // 按钮事件

  // layui.util.event('lay-on', {

  //   getChecked: function(othis){

  //     var checkedData = tree.getChecked('demo-id-1'); // 获取选中节点的数据

      

  //     layer.alert(JSON.stringify(checkedData), {shade:0});

  //     console.log(checkedData);

  //   },

  //   setChecked: function(){

  //     tree.setChecked('demo-id-1', [12, 16]); // 勾选对应 id 值的节点

  //   },

  //   reload: function(){

  //     tree.reload('demo-id-1', {}); // 重载实例

  //   }

  // });

  });
}
},
watch:{
    
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 #cesiumContainer {
            width: 100%;
            height: 100%;
            margin: 0;
            padding: 0;
            overflow: hidden;
            display: flex;
            width: 100%;
            height: 100%;
        }
        .bj{
            position: absolute;
            top: 1rem;
            z-index: 99;
      width: 20rem;
    background-size: contain;
    
        }
      .bjr{
        background: url("../assets/images/bj.png") no-repeat;
           right: 3rem;
           width:35rem;
    background-size: contain;
      }
          
</style>
<style>
    .el-checkbox{
        color: aliceblue !important;

    }
    .myinfo{
      background: url("../../static/images/1.png") no-repeat;
    }
    .myinfo2{
      background: url("../../static/images/2.png") no-repeat;
    }
   
    
    .myinfo:hover{
      background: url("../../static/images/1-选中.png") no-repeat;
    }
    .myinfo2:hover{
      background: url("../../static/images/2-选中.png") no-repeat;
    }
    .layui-tree-txt{
      color:#ffffff;
    }
</style>


