<template>
  <div class="hello">
  <div style="width:100%;position:absolute;left:0;z-index:100;height:5vh;    background: #000000;text-align:center;line-height:5vh;
  font-size:20px;
    color: #ffffff;"><i class="el-icon-s-management
"></i> 演示</div>

  <div style="width:17rem;position:absolute;left:0;z-index:100;height:95vh;border-right:1px solid #fff;top:5vh;background: rgba(1,1,1,0.5);">

    <el-menu
      default-active="2"
      class="el-menu-vertical-demo"
      background-color="#545c64"
      text-color="#fff"
      @select="select"
      
      active-text-color="#ffd04b">
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span>绘制管理</span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="标记管理">标记管理</el-menu-item>
        </el-menu-item-group>
        <el-menu-item-group>
          <!-- <el-menu-item index="点管理">点管理</el-menu-item> -->
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
  </div>
<el-drawer
  :visible.sync="drawer"
  :direction="direction"
  size="100%"
  :before-close="handleClose">
  <div style="padding:10px;">

    <el-collapse v-model="activeNames" >
  <el-collapse-item title="线标记" name="1">
    <div>
     <!-- 绘制线 -->
  <el-row>
  <el-col :span="8"><div class="grid-content bg-purple"> <el-input placeholder="请输入点大小" v-model="linesize">
    <template slot="prepend">线大小</template>
  </el-input></div></el-col>
    <el-col :span="8"><div class="grid-content bg-purple"> <el-input placeholder="请输入点大小" v-model="linepsize">
    <template slot="prepend">点大小</template>
  </el-input></div></el-col>
  <el-col :span="8"><div class="grid-content bg-purple-light">
  <el-switch
  v-model="value1"
  active-text="是否有点"
  >
</el-switch>
  </div></el-col>
   <el-col :span="8"><div class="grid-content bg-purple-light">
<el-color-picker v-model="linecolor" show-alpha @active-change="achange"></el-color-picker> 线颜色设置
  </div></el-col>
  <el-col :span="8"><div class="grid-content bg-purple-light">
  <el-switch
  v-model="ycx"
  active-text="覆盖绘制"
  >
</el-switch>
  </div></el-col>
</el-row>
     <el-button type="primary" @click="hzx('x')">左键绘制线</el-button >
     <el-button type="primary" @click="removehandler()">结束绘制</el-button >
     <el-button type="primary" @click="reset()">重新绘制</el-button >
    </div>
    <!-- <div>在界面中一致：所有的元素和结构需保持一致，比如：设计样式、图标和文本、元素的位置等。</div> -->
  </el-collapse-item>
  <!-- 绘制点 -->
  <el-collapse-item title="点标记" name="2">
    <div>  <el-row>
  <el-col :span="8"><div class="grid-content bg-purple"> <el-input placeholder="请输入点大小" v-model="pointsize">
    <template slot="prepend">线大小</template>
  </el-input></div></el-col>
    <el-col :span="8"><div class="grid-content bg-purple-light">
<el-color-picker v-model="pcolor" show-alpha @active-change="pchange"></el-color-picker> 点颜色设置
  </div></el-col>
  <el-col :span="8"><div class="grid-content bg-purple-light"></div></el-col>
</el-row>
     <el-button type="primary" @click="hzx('d')">左键绘制点</el-button >
          <el-button type="primary" @click="removehandler()">结束绘制</el-button >
     </div>
    <!-- <div>页面反馈：操作后，通过页面元素的变化清晰地展现当前状态。</div> -->
  </el-collapse-item>
  <!-- 绘制面 -->
  <el-collapse-item title="面标记" name="3">
  <el-row>
  <el-col :span="8"><div class="grid-content bg-purple"></div></el-col>
    <el-col :span="8"><div class="grid-content bg-purple"></div></el-col>
  <el-col :span="8"><div class="grid-content bg-purple-light">
  </div></el-col>
   <el-col :span="8"><div class="grid-content bg-purple-light">

  </div></el-col>
  <el-button type="primary" @click="hzx('m')">左键绘制面</el-button >
   <el-button type="primary" @click="reset()">结束绘制</el-button >
</el-row>
  </el-collapse-item>
</el-collapse>
  </div>

</el-drawer>
  <div id="cesiumContainer">
  </div>
  </div>
</template>

<script>
// import { Math as CMath, CallbackProperty } from "cesium";
import getCzml from '../assets/utils/GetCzml';
import AddCzmlDataSource from '../assets/utils/AddCzmlDataSource';
import {addPoint,addLine,addPlygen} from './util/utils'
export default {
  name: 'HelloWorld',
  data () {
    return {
		satelliteEntities:new Map(),
        checked:false,
        checked1:false,
        checked2:false,
        viewer:null,
        checked9:false,
        qh:true,
        checked10:false,
          drawer: true,
        direction: 'rtl',
        activeNames: ['1'],
        linedatas:[],
        pointsize:10,
        flag:'',
        handler:'',
        value1:false,
        linesize:10,
        linepsize:10,
        linecolor: 'rgba(19, 206, 102, 0.8)',
        pcolor:'rgba(19, 206, 102, 0.8)',
        ycx:false,
        polydatas:[]
    }
  },
  mounted () {
	  let that = this;
    this.$nextTick(() => {
		var viewer;
		                viewer = new Cesium.Viewer('cesiumContainer', {
							// imageryProvider: new Cesium.SingleTileImageryProvider({
							//             url: Cesium.buildModuleUrl('Assets/Textures/earth_color_low_4096.jpg')
							//         }),
		                    animation: true,
		                    timeline: false,
		                    baseLayerPicker: false,
		                    geocoder: false,
		                    sceneModePicker: false,
		                    navigationHelpButton: false
		                });
                        that.viewer=viewer;
						viewer.imageryLayers.removeAll();

						viewer.scene.globe.depthTestAgainstTerrain = true;
						viewer._cesiumWidget._creditContainer.style.display = "none";
			var tdtLayer = new Cesium.UrlTemplateImageryProvider({
                url: "http://webrd02.is.autonavi.com/appmaptile?lang=zh_cn&size=1&scale=1&style=8&x={x}&y={y}&z={z}",
                minimumLevel: 3,
                maximumLevel: 18
            })
            viewer.imageryLayers.addImageryProvider(tdtLayer);
			//高德影像
			             tdtLayer = new Cesium.UrlTemplateImageryProvider({
			                url: "https://webst02.is.autonavi.com/appmaptile?style=6&x={x}&y={y}&z={z}",
			                minimumLevel: 3,
			                maximumLevel: 18
			            })
			            viewer.imageryLayers.addImageryProvider(tdtLayer);
		 //高德路网中文注记
		            tdtLayer = new Cesium.UrlTemplateImageryProvider({
		                url: "http://webst02.is.autonavi.com/appmaptile?x={x}&y={y}&z={z}&lang=zh_cn&size=1&scale=1&style=8",
		                minimumLevel: 3,
		                maximumLevel: 18
		            })

		            viewer.imageryLayers.addImageryProvider(tdtLayer);
                viewer.clock.shouldAnimate=true;
                document.getElementsByClassName('cesium-viewer-animationContainer')[0].style.display="none";
				
	});
	},
	methods:{
    achange(){
      console.log(this.linecolor)
    },
    pchange(){
      console.log(this.pcolor)
    },
      select(val){
         this.drawer=true;
        console.log("点击了",val);
        if(val=="线管理"){
        }
        if(val=="点管理"){

        }
      },
        handleClose(done) {
       this.drawer=false;
      },
      hzx(flag){
      this.flag=flag;
      if(!this.handler){

          let handler = new Cesium.ScreenSpaceEventHandler(this.viewer.scene.canvas);
          this.handler=handler
          handler.setInputAction(event => {
            console.log(event,event.position)
            this.carttolt(event);
          }, Cesium.ScreenSpaceEventType.LEFT_CLICK);
      }

      },
      addPoint1(){
           this.viewer.entities.add({
                // 设置圆点的位置
                position:Cesium.Cartesian3.fromDegrees(116, 39,1500),
                point:{
                    // 圆点的大小和颜色
                  pixelSize:100,
                  color: Cesium.Color.RED
                }
            })
      },
      // 世界坐标转换经纬度
      carttolt(movement){

            //获得点的世界坐标  这里是世界的坐标不可直接使用需要转换成经纬度
          //pickEllipsoid   是返回椭球或地图表面上的点在世界坐标系中
          const start_point = this.viewer.scene.camera.pickEllipsoid(
            movement.position, //第一个参数 世界坐标
            this.viewer.scene.globe.ellipsoid  //第二个参数 globe是获取到这个地图 ellipsoid 获取到描述地球形状的椭圆体
          );
    
          //笛卡尔坐标转弧度  fromCartesian是从笛卡尔位置创建一个新的制图实例。其中的值结果对象将以弧度表示。
          let cartographic = Cesium.Cartographic.fromCartesian(//fromCartesian 有三个参数  
            start_point,//第1个是要转换为制图表达的笛卡尔位置 
            this.viewer.scene.globe.ellipsoid,//第2个是位置所在的椭圆体 
            new Cesium.Cartographic()// 第三个是储存的位置
          );
    
          // Cesium.Math.toDegrees 这个是 弧度转度，将弧度转换成经纬度
          let lng = Cesium.Math.toDegrees(cartographic.longitude);//longitude与latitude是精度和纬度
          let lat = Cesium.Math.toDegrees(cartographic.latitude);
          console.log(lng,lat)
          if(this.value1&&this.flag=='x'){
          addPoint(this.viewer,[lng,lat,0],this.linepsize,'#00fff0')

          }
          // 绘制点
          if(this.flag=='d'){
            console.log("绘制点")
              addPoint(this.viewer,[lng,lat,0],this.pointsize,this.pcolor)
          }

          // 绘制线
          if(this.flag=='x'){
            if(this.ycx){
              // 绘制绘覆盖
                 this.linedatas.push(lng)
                  this.linedatas.push(lat)
                  this.linedatas.push(0)
            this.addLine(this.linedatas,this.linesize)

            }else{
                // 不覆盖
               
                this.linedatas.push(lng)
                  this.linedatas.push(lat)
                  this.linedatas.push(0)
                  console.log(this.linedatas.length)
                  if(this.linedatas.length>5){

                  let arr = []
                  arr= this.linedatas.slice(-6)
                  // if(this.linedatas.length>5){
                  //    arr[0]=this.linedatas[this.linedatas.length-6]
                  // arr[1]=this.linedatas[this.linedatas.length-5]
                  // arr[2]=this.linedatas[this.linedatas.length-6]
                  // arr[3]=this.linedatas[this.linedatas.length-6]
                  // arr[4]=this.linedatas[this.linedatas.length-6]
                  // arr[5]=this.linedatas[this.linedatas.length-6]
                  // arr[6]=this.linedatas[this.linedatas.length-6]
                  // }
                 
                  this.addLine(arr,this.linesize)
                  }


            }
            
          }
// 绘制面
          if(this.flag=='m'){
              this.polydatas.push(lng)
                  this.polydatas.push(lat)
            addPlygen(this.viewer,this.polydatas)
          }


      },
      addLine(data,size){
            addLine(this.viewer,data,size,this.linecolor)
      },
      removehandler(){
        if(this.handler){
        this.handler.removeInputAction(Cesium.ScreenSpaceEventType.LEFT_CLICK)
        this.handler=null
        }
      },
      reset(){
        if(this.handler){
           this.linedatas=[]
          this.removehandler()
        }
       
      }
},
watch:{
    
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 #cesiumContainer {
            width: 100%;
            height: 100%;
            overflow: hidden;
            margin: 0;
            padding: 0;
            overflow: hidden;
            display: flex;
            top: 0;
            left: 0;
        }
</style>
<style>
    .el-checkbox{
        color: aliceblue !important;

    }
    .myinfo{
      background: url("../../static/images/1.png") no-repeat;
    }
    .myinfo2{
      background: url("../../static/images/2.png") no-repeat;
    }
   
    
    .myinfo:hover{
      background: url("../../static/images/1-选中.png") no-repeat;
    }
    .myinfo2:hover{
      background: url("../../static/images/2-选中.png") no-repeat;
    }
    .layui-tree-txt{
      color:#ffffff;
    }
    ul li,.el-menu .el-menu--inline,.el-submenu__title,.el-menu{
      background: rgba(1,1,1,0.5) !important;
    }
    .el-drawer{
    
       color: #ffffff;
    }
    .el-drawer__wrapper{
      width: 30%!important;
      left: 70%!important;

    }
    .v-modal{
      display: none !important;
    }
    .hzx{
      cursor: pointer;
    }
</style>


