<template>
  <div>
    <!-- <el-table
      :data="colums"
      @selection-change="handleSelectionChange"
      ref="dataTable"
      style="width: 100%">
           <el-table-column
      type="selection"
      width="55">
      </el-table-column>
      <el-table-column
        prop="name"
       label="名称"
        width="180">
      </el-table-column>
      
    </el-table> -->

    <!-- 优化  @user:hz -->
    <el-tree
      :key="timer"
      :data="treeData"
      show-checkbox
      default-expand-all
      node-key="id"
      ref="tjtree"
      highlight-current
      :props="defaultProps"
      :default-checked-keys="defaultChecked"
      @check	="getChecked"	

      >
    </el-tree>
  </div>
         
</template>

<script> 
import data from "./all"
// import EventBus from '../../../util/eventbus'

export default {
    data () {
        return {
          sekey:1,
          colums:[],
          treeData:[],
          defaultProps:{
            children: 'children',
            label: 'label'
          },
          defaultChecked:[],
          timer: new Date().getTime()  
        };
    },
    props:{

      data: {
            type: Object,
            default () {
                return {};
            }
        },
      isSelects:{
       type: Boolean,
            default () {
                return {};
            }
      }
    },
    mounted () {
      // EventBus.$on("tjoptioned",()=>{
      //   this.timer = new Date().getTime()
      // })
      let datas=JSON.parse(localStorage.getItem("nowSelects"))

      // 优化  @user:hz
      let allArr = data.all;
      let nowChecked = []
      let arr = [{label:'全部指标',id:'qbzb',level:0,children:[]}];
      allArr.forEach((value)=>{
        datas.forEach((val)=>{
          if(val.name == value.name){
            nowChecked.push(value.value)
          }
        })
        
        if(value.level == 1){
          arr[0].children.push({"label":value.label,"id":value.id,"level":value.level})
        }else{
          if(arr[0].children.length == 1){
            arr[0].children[0].children ?  '' : arr[0].children[0].children = []
            arr[0].children[0].children.push({"label":value.name,"id":value.value,"level":2})
          }else if(arr[0].children.length == 2){
            arr[0].children[1].children ?  '' : arr[0].children[1].children = []
            arr[0].children[1].children.push({"label":value.name,"id":value.value,"level":2})
          }else{
            arr[0].children[2].children ?  '' : arr[0].children[2].children = []
            arr[0].children[2].children.push({"label":value.name,"id":value.value,"level":2})
          }
        }
      })
      this.treeData = arr
      this.defaultChecked = nowChecked
      console.log(nowChecked,"默认的给定值")

       this.colums = data.all;
       console.log('colums',this.colums)
       this.init(datas)
       this.$nextTick(()=>{
         window.vm.$on('isSelects', this.iselects);
       })
 
    },
    methods: {
      getChecked(){
        let arr = this.$refs.tjtree.getCheckedNodes()
        console.log(arr,"选择后的值")
        let checkedArr = []
        arr.forEach((value)=>{
          if(value.level < 2) return
          checkedArr.push({"name":value.label,"value":value.id})
        })
        this.$emit("val",checkedArr)
      },
      
      handleSelectionChange(val){
        // console.log(val,"选择后的值")
        this.$emit("val",val)
      },
      init(datas){
        this.$nextTick(()=>{
          data.all.forEach(item=>{
            this.$refs.dataTable.toggleRowSelection(item, false)
          })
          data.all.forEach((item) => {
           datas.forEach(item2=>{
             if(item.name==item2.name){
                this.$refs.dataTable.toggleRowSelection(item, true)
             }
           })
          })
        })
      },
      iselects(){
        let datas=JSON.parse(localStorage.getItem("nowSelects"))
      }
    }
    
};
</script>

<style lang="scss" scoped>

</style>



