<template>
    <el-table
      :data="tableData"
      height="750"
      style="width: 100%">
      <el-table-column
        :prop="item.value"
        v-for="(item,index) in rowdatas"
        :label="item.name"
        v-if="!item.ismore"
        width="180"
        :key="index"
        >
      </el-table-column>
      <el-table-column
        v-else
          :label="item.name"
          width="180"
      >
      <el-table-column
          v-if="item.name=='边界管控指标'"
          v-for="(item2,index) in bjgk"
          :label="item2.name"
          :prop="item2.value"
          width="180"
          :key="index"
      ></el-table-column>
      <el-table-column
          v-if="item.name=='时序管控指标'"
          v-for="(item2,index) in sxgk"
          :label="item2.name"
          :prop="item2.value"
          width="180"
          :key="index"
      ></el-table-column>
      <el-table-column
          v-if="item.name=='建设管控指标'"
          v-for="(item2,index) in jsgk"
          :label="item2.name"
          :prop="item2.value"
          width="180"
          :key="index"
      ></el-table-column>
      <el-table-column
          v-if="item.name=='底线管控指标'"
          v-for="(item2,index) in dxgk"
          :label="item2.name"
          :prop="item2.value"
          width="180"
          :key="index"
      ></el-table-column>
      <el-table-column
          v-if="item.name=='节约集约指标'"
          v-for="(item2,index) in jyjy"
          :label="item2.name"
          :prop="item2.value"
          width="180"
          :key="index"
      ></el-table-column>
      </el-table-column>
    </el-table>
</template>

<script>
import data  from './data.js'
// import {getList} from '@/api/pgsj';
export default {
    data () {
        return {
           tableData: [],
          rowdatas:[
            {"name":"行政区划","value":"SXZQMC",ismore:false},
            {"name":"项目名称","value":"XMMC",ismore:false},
            {"name":"项目编号","value":"XMBM",ismore:false},
            {"name":"边界管控指标","value":"bjgkzb",ismore:true},
            {"name":"时序管控指标","value":"sxgkzb",ismore:true},
            {name:"建设管控指标",value:"jsgkzb",ismore:true},
            {name:"底线管控指标",value:"dxgkzb",ismore:true},
            {name:"节约集约指标",value:"jyjyzb",ismore:true},
            {"name":"总评分","value":"ZPF",ismore:false},
          ],
          bjgk:[],
          sxgk:[],
          jsgk:[],
          dxgk:[],
          jyjy:[],
          csize:10,
          cindex:1,
          
        };
    },
    props:{
      data: {
            type: Object,
            default () {
                return {};
            }
        }
    },
    mounted () {
      // this.$nextTick(()=>{
      //    window.vm.$on('tdata', this.tdata);
      //     window.vm.$on('isxm', this.isxm);
      //  })
      //  if(this.data.data){
      //     this.tableData=this.data.data.data.records;
      //  }
             
  
      //  this.bjgk=data.bjgk
      //  this.sxgk=data.sxgk
      //  this.jsgk=data.jsgk
      //  this.dxgk=data.dxgk
      //  this.jyjy=data.jyjy
    },
    methods: {
      tdata(data){
         this.tableData=data.data.data.records;
      },
      isxm(val){
        if(val){
           this.rowdatas=[
            {"name":"行政区划","value":"SXZQMC",ismore:false},
            {"name":"项目名称","value":"XMMC",ismore:false},
            {"name":"边界管控指标","value":"bjgkzb",ismore:true},
            {"name":"时序管控指标","value":"sxgkzb",ismore:true},
            {name:"建设管控指标",value:"jsgkzb",ismore:true},
            {name:"底线管控指标",value:"dxgkzb",ismore:true},
            {name:"节约集约指标",value:"jyjyzb",ismore:true},
          ]
        }else {
          this.rowdatas=[
            {"name":"行政区划","value":"SXZQMC",ismore:false},
            {"name":"边界管控指标","value":"bjgkzb",ismore:true},
            {"name":"时序管控指标","value":"sxgkzb",ismore:true},
            {name:"建设管控指标",value:"jsgkzb",ismore:true},
            {name:"底线管控指标",value:"dxgkzb",ismore:true},
            {name:"节约集约指标",value:"jyjyzb",ismore:true},
          ]
        }
      }
    },
    watch:{
      data:function(val){
         if(this.data.data){
          this.tableData=this.data.data.data.records;
       }
      }
    }
};
</script>

<style lang="scss" scoped>

</style>



