<template>
  <div class="header">
    <div class="headerLeft">
      <div class="headOneLeft">
        <span class="logo"></span>
      </div>
      <div class="headOneRight">
        <p class="title">某某xxxxxx分析系统</p>
        <p class="english">
          xxxxxx  ssss xxxxxx  ssss xxxxx moumou  sss  sssasadssadas projects
        </p>
      </div>
    </div>
    <div class="headerCenter">
      <el-menu
        :default-active="activeIndex"
        class="custom-menu-header"
        mode="horizontal"
        @select="handleSelect"
      >
        <!-- <el-menu-item index="1">
          <i class="el-icon-stopwatch"></i>
          <span class="text">驾驶舱</span>
        </el-menu-item> -->
        <el-menu-item index="1">
          <i class="el-icon-pie-chart"></i>
          <span class="text">统计报表</span>
        </el-menu-item>
      </el-menu>
    </div>
    <div class="headerRight">
      <el-image class="topImg" :src="userInfo.avatar" fit="cover"></el-image>
      <span>{{ userInfo.user_name }}</span>
     
      <div class="logout" @click="submitOut()">
        <i class="el-icon-switch-button"></i>
      </div>
    </div>
 

  </div>
</template>
<script>
// 比例尺

export default {
    name: 'headerMenu',
    components: {
    },
    data () {
        return {
          activeIndex: '1',
            anyResultShow: false,
            anyResultData: null,
            anyResultFeatures: null,
            islegend: false,
            title: '',
            color: [],
            scaleState: false,
            userInfo:{ user_name: 'userInfo',avatar:"" }
        };
    },
    computed: {
    },
    mounted () {
      
    },
    methods: {
      submitOut(){
        
      },handleSelect(){

      }

    }
};
</script>
<style lang="scss" scoped>
.header {
  position: fixed;
  top: 0;
  z-index: 1005;
  width: 100%;
  height: 70px;
  background: url("../../assets/images/header.png") no-repeat center left #416DFF;
  text-align: left;
  color: #ffffff;
  box-sizing: border-box;

  .any-area-result {
    right: 65px;
    top: 180px;
  }

  .sdxz {
    font-size: 16px;
    left: 440px;
  }

  .sddl {
    font-size: 16px;
    right: 60px;
  }

  .headerLeft {
    position: absolute;
    width: 500px;
    padding-top: 16px;
    padding-left: 16px;
    box-sizing: border-box;
    height: 100%;
  }

  .headerCenter {
    position: absolute;
    left: 520px;
    box-sizing: border-box;
    height: 100%;
    width: 1497px;
    overflow: auto;
  }

  .headerRight {
    position: absolute;
    right: 0;
    line-height: 70px;
    padding-right: 16px;
    color: #fff;
    font-size: 18px;
    box-sizing: border-box;
    .topImg {
      width: 24px;
      height: 24px;
      margin-right: 5px;
      vertical-align: middle;
      border-radius: 50%;
    }
    .el-dropdown {
      color: #fff;
      font-size: 18px;
      font-family: "PingFangSC";
    }
    .logout {
      display: inline-block;
      width: 24px;
      height: 24px;
      line-height: 24px;
      background: linear-gradient(to right, #6ea2f9, #49deff);
      margin-left: 20px;
      text-align: center;
      cursor: pointer;
      vertical-align: middle;
      border-radius: 50%;
      i {
        font-size: 16px;
      }
    }
  }

  .headOneLeft {
    float: left;
    .logo {
      display: inline-block;
      width: 40px;
      height: 40px;
      margin-right: 8px;
      background: url("../../assets/images/logo.png") no-repeat center left;
      background-size: contain;
      vertical-align: top;
    }
  }

  .headOneRight {
    float: left;
    font-family: "PingFangSC";
    .title {
      font-size: 24px;
      font-weight: 700;
      margin-bottom: 6px;
      letter-spacing: 2px;
    }

    .english {
      font-size: 12px;
      color: rgba($color: #fff, $alpha: 0.85);
    }
  }

  // 自定义菜单样式
  .custom-menu-header {
    border-bottom: 0;
    background: transparent;

    // 一级菜单样式
    .el-menu-item {
      font-size: 24px;
      font-family: "PingFangSC";
      text-shadow: 0px 0px 1px #000;
      color: #ffffff;
      padding: 0 18px;
      cursor: pointer;
      height: 70px;
      line-height: 70px;
      box-sizing: border-box;

      i {
        color: #ffffff;
        margin-right: 3px;
        font-size: 22px;
        vertical-align: baseline;
      }
    }

    .el-menu-item:hover,
    .el-menu-item.is-active {
      background-color: rgba($color: #9ebae5, $alpha: 0.35) !important;
      border-bottom: 5px solid #7ee7ff !important;
      font-weight: bold;
      position: relative;
      .text {
        text-shadow: none;
        background: linear-gradient(to bottom, #51c0ff, #f3f8ff);
        background-clip: text;
        -webkit-background-clip: text;
        color: transparent;
      }
      i {
        color: #69d2ff;
        font-weight: bold;
      }
    }

    //菜单禁用时的样式
    .is-disabled {
      opacity: 0.8;
      cursor: not-allowed;

      &:hover {
        border-bottom: 0 !important;
      }
    }
  }

  .headTopRight {
    text-align: right;

    div {
      margin-bottom: 3px;

      i {
        margin-right: 10px;
        font-size: 22px;
        text-shadow: 1px 1px 0 #000000, -1px -1px 0 #000000;
      }
    }
  }
}
.popper-level1,
.popper-level2,
.popper-level3 {
  /deep/ .el-submenu__title {
    font-size: 16px !important;
  }
}
.popper-level1 .el-menu--popup .el-menu-item {
  font-size: 16px;
}
.el-menu--horizontal .el-menu .el-menu-item,
.el-menu--horizontal .el-menu .el-submenu__title {
  font-size: 16px !important;
}
.el-menu--horizontal .el-menu .el-submenu__title {
  font-size: 16px !important;
}
.el-menu-item:hover {
  color: #ffffff;
  background: #49deff !important;
}
/deep/.el-submenu div:hover {
  color: #ffffff;
  background: #49deff !important;
}
</style>



