<template>
  <!-- <el-menu
      default-active="1"
      class="el-menu-vertical-demo"
      @select="select">
      <el-menu-item index="1">
          <i class="el-icon-location"></i>
           <span slot="title">项目基本信息统计</span>
      </el-menu-item>
      <el-menu-item index="2">
        <i class="el-icon-menu"></i>
        <span slot="title">监测图斑信息统计</span>
      </el-menu-item>
      <el-menu-item index="3">
        <i class="el-icon-document"></i>
        <span slot="title">项目评估数据统计</span>
      </el-menu-item>
      <el-menu-item index="4">
        <i class="el-icon-setting"></i>
        <span slot="title">综合数据统计</span>
      </el-menu-item>
    </el-menu> -->
      <el-menu
      :default-active="menuList[0].id"
      class="define-menu"
      @select="select"
    >
      <div v-for="(it, i) in menuList" :key="i">
        <el-menu-item v-if="!it.children" :index="it.id">
          <i :class="it.icon"></i>
          <span slot="title">{{ it.name }}</span>
        </el-menu-item>
        <el-submenu :index="it.id" v-else>
          <template slot="title">
            <i :class="it.icon"></i>
            <span>{{ it.name }}</span>
          </template>
          <el-menu-item
            v-for="item in it.children"
            :key="item.id"
            :index="item.id"
          >
            <i :class="item.icon"></i>
            <span slot="title">{{ item.name }}</span>
          </el-menu-item>
        </el-submenu>
      </div>
    </el-menu>
</template>

<script>
// import EventBus from '../../../util/eventbus'
import all from './all';
export default {
    data () {
        return {
          menuList: [
                {
                    id: '1',
                    icon: 'el-icon-s-cooperation',
                    name: '项目基本信息统计'
                },
                {
                    id: '2',
                    icon: 'el-icon-s-help',
                    name: '监测xx信息统计'
                },
                {
                    id: '3',
                    icon: 'el-icon-help',
                    name: '项目评估数据统计'
                },
                {
                    id: '4',
                    icon: 'el-icon-tickets',
                    name: '综合数据统计'
                }

            ]
        };
    },
    mounted () {
    },
    methods: {
       select(key) {
        this.nowSelects=JSON.stringify(all.table)
        localStorage.setItem("nowSelects",this.nowSelects)
         this.$emit("data",{key:key})
        //  EventBus.$emit("tjoptioned",{key:key})

       }
    }
};
</script>

<style lang="scss" scoped>
.define-menu{
  top: 0;
  padding-top: 0;
}
</style>



