<template>
  <div class="hello">
    <el-form ref="form" :model="form" label-width="80px" style="margin-top: 20rem;margin-right:25rem;float:right;">
      <el-form-item>
      <div style="font-size:20px;color:#fff;">项目演示平台</div>
    </el-form-item>
  <el-form-item>
    <div>
        <span class="comimg"><i class="el-icon-user" style="color:#fff;"></i> </span>
    <span style="width:17rem;position: absolute;">
      <el-input v-model="form.name"></el-input>
    </span>
    </div>
    <div style="margin-top:1rem;">
      <span class="comimg"><i class="el-icon-bell" style="color:#fff;"></i> </span>
    <span style="width:17rem;position: absolute;">
      <el-input v-model="form.psw"></el-input>
    </span>
    </div>
  </el-form-item>
  <el-form-item>
    <el-button type="warning" style="width:19.5rem;background: #8baef2;" @click="gorouter">登陆</el-button>
  </el-form-item>
</el-form>

<el-drawer
  :visible.sync="drawer"
  :direction="direction"
  size="100%"
  >
 <video  width="1900" height="900" controls autoplay muted playsinline loop>
   <source src="../../static/bg.mp4" type="video/mp4">
     您的浏览器不支持视频标签。

</video>
</el-drawer>

  </div>
</template>

<script>
export default {	
  name: 'HelloWorld',
  data () {
    return {
      form:{
        name:"",
        psw:""
      },
      drawer:false,
      direction:'ttb'
    }
  },
  mounted () {
	  setTimeout(()=>{
      this.drawer=true;
    },100)
  },
  methods:{
	  
	  gorouter(){
      if(this.form.name=="admin"&&this.form.psw=="123456"){
          this.$router.push({path:'/index'})
      }else{
        this.$message({
          message: '用户名密码错误，请重试！',
          type: 'warning'
        });
      }
	  }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  background: url("../../static/images/ibj.png") no-repeat;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
}
.comimg{
  height: 40px;
    display: inline-block;
    width: 40px;
    background: #8baef2;
    text-align: center;
}
</style>




