        that.checked=obj.checked;
        
that.handleCheckAllChange()
      }
       if(obj.data.title=="星2"){
        that.checked1=obj.checked;
        
that.handleCheckAllChange1()
      }
      if(obj.data.title=="星3"){
        that.checked2=obj.checked;
        
that.handleCheckAllChange2()
      }

         if(obj.data.title=="上星列表"){
        that.checked=obj.checked;
that.handleCheckAllChange()
setTimeout(function(){
        that.checked1=obj.checked;

  that.handleCheckAllChange1()
},500)

      }
       if(obj.data.title=="下星列表"){
        that.checked2=obj.checked;
        
that.handleCheckAllChange2()
      }
