import Vue from 'vue'
import Router from 'vue-router'
import wx from '@/components/wx';
import index from '@/components/index';
import login from '@/components/login';
// import modao from '@/components/modao';
// import crm from '@/components/crm';
import loginh from '@/components/hy/login';
import indexh from '@/components/hy/index';
import zhyq from "@/components/zhyq";
import yzt from "@/components/yzt";
import dpxm from "@/components/dpxm";
import zdy from "@/components/zdy";
import sw from "@/components/sw";
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/index',
      name: 'index',
      component: index
    },
    {
      path: '/wx',
      name: 'wx',
      component: wx
    }, {
      path: '/indexh',
      name: 'indexh',
      component: indexh
    },
    {
      path: '/loginh',
      name: 'loginh',
      component: loginh
    },
    // {
    //   path: '/modao',
    //   name: 'modao',
    //   component: modao
    // },
    // {
    //   path: '/crm',
    //   name: 'crm',
    //   component: crm
    // },
    {
      path: '/zhyq',
      name: 'zhyq',
      component: zhyq
    },
    {
      path: '/yzt',
      name: 'yzt',
      component: yzt
    },
    {
      path: '/dpxm',
      name: 'dpxm',
      component: dpxm
    },
    {
      path: '/zdy',
      name: 'zdy',
      component: zdy
    },
    {
      path: '/sw',
      name: 'sw',
      component: sw
    }
  ]
})



