<template>
     <el-table
      :data="tableData"    
      class="fxtable"
      
      style="width:calc( 100vw - 200px);overflow: auto;height: calc(100vh - 190px)">
      <el-table-column
        :prop="item.value"
        v-for="(item,index) in colums"
        :label="item.name"
        :key="index"
        width="280">
      </el-table-column>
    </el-table>
</template>

<script> 
import all from "./all"
export default {
    data () {
        return {
          sekey:1,
           tableData: [],
          colums:[],
          height:"760"
        };
    },
     props:{
      data: {
            type: Object,
            default () {
                return {};
            }
        }
    },
    mounted () {
      this.colums = all.table;
      // console.log("选择了---",localStorage.getItem("nowSelects"))

      // revice  @user:hz
      // let arr1 = all.table;
      // let arr2 = localStorage.getItem("nowSelects")
      // this.colums = [].concat(arr1,arr2)


      // console.log(all.table)
        if(this.data.data){
          this.tableData=this.data.data.data.records;
       }
        this.$nextTick(()=>{
          window.vm.$on('sekey', this.setkey);
          window.vm.$on('isSelects', this.iselects);
          
       })
    },
    methods: {
      setkey(val){
        this.sekey=val
      
         
      },
      iselects(){
        this.$nextTick(()=>{
        let datas=JSON.parse(localStorage.getItem("nowSelects"))
          this.colums=datas
          setTimeout(()=>{
            this.height="750"
          },1000)
          
        })
      }
   
    },
    watch:{
      data:function(val){
         if(this.data.data){
          this.tableData=this.data.data.data.records;
       }
      }
    }
};
</script>

<style lang="scss" scoped>
  /deep/{
  .fxtable .el-table__body-wrapper{
      overflow: auto !important;
    }
}
  /deep/ .el-table--scrollable-x .el-table__body-wrapper {
        overflow: auto !important;
    }
    /deep/.el-table__fixed-right-patch {
    background-color: #f5f7fa !important;
    }
</style>
<style>
 .fxtable .el-table__body-wrapper{
      /* height: 699px !important; */
      overflow: auto ;
    }
</style>



