<script>
import L from "leaflet";
export default {
  name: 'yzt',
  data () {
    return {
      map:'',
       drawer: false,
        direction: 'rtl',
        size:'50%',
        size2:'100%',
        value1:false,
        value2:false,
        value3:false,
        input1:'',
        drawer1:false

    }
  },
  mounted () {
    let that=this;
      // const VEC_C =
      //   'http://t0.tianditu.gov.cn/vec_w/wmts?SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&LAYER=vec&STYLE=default&TILEMATRIXSET=w&FORMAT=tiles&TILEMATRIX={z}&TILEROW={y}&TILECOL={x}&tk=';
      // const CVA_C =
      //   'http://t0.tianditu.gov.cn/cva_w/wmts?SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&LAYER=cva&STYLE=default&TILEMATRIXSET=w&FORMAT=tiles&TILEMATRIX={z}&TILEROW={y}&TILECOL={x}&tk=';
      // const TK_KEY = '天地图key';
      // var layer1 = L.tileLayer(VEC_C + TK_KEY, {
      //   tileSize: 256,
      //   layers: 'layers1'
      // });
      // var layer2 = L.tileLayer(CVA_C + TK_KEY, {
      //   tileSize: 256,
      //   layers: 'layers2'
      // });
	   this.map = L.map('map',{
      center: [40.02404009136253, 116.50641060224784], // 地图中心
      zoom: 14,   //缩放比列
      //  layers: [layer1, layer2,],
      zoomControl: false, //禁用 + - 按钮
      doubleClickZoom: false,  // 禁用双击放大
      attributionControl: false  // 移除右下角leaflet标识
    });
    
     let name = L.tileLayer(
        "http://webrd01.is.autonavi.com/appmaptile?lang=zh_cn&size=1&scale=1&style=7&x={x}&y={y}&z={z}",
      ).addTo(this.map);
// map.on("click", function (evt) {
//         var x = evt.latlng.lng;
//         var y = evt.latlng.lat;
//         marker = L.marker(evt.latlng).addTo(map);
//     });

          // 现在添加点击事件监听器
      this.map.on('click', function(e) {
          // 当地图被点击时，这里的代码会被执行
          // e.latlng 包含了被点击的纬度和经度
          // console.log('您点击了地图上的位置：' + e.latlng.lat);
          // console.log(e.target)
          if(that.value3){
              L.marker([e.latlng.lat,e.latlng.lng]).addTo(that.map);
           that.map.flyTo([e.latlng.lat,e.latlng.lng])
          }
      });
	},
	methods:{
    show(f){
      if(f=='tc'){
        this.drawer=true;
      }
      if(f=="fx"){
        this.drawer1=true;
      }
    },
    change(val,val2){
      console.log(val,val2)
      if(val2==1){
        if(val){
           L.marker([50.5, 30.5]).addTo(this.map);
           this.map.flyTo([50.5, 30.5])
        }
       
      }
      if(val2==3){
        this.value3=val
      }
       if(val2==2){
if(val){
        //    const VEC_C =
        // 'http://t0.tianditu.gov.cn/vec_w/wmts?SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&LAYER=vec&STYLE=default&TILEMATRIXSET=w&FORMAT=tiles&TILEMATRIX={z}&TILEROW={y}&TILECOL={x}&tk=';
        L.tileLayer(
        'http://t0.tianditu.gov.cn/vec_w/wmts?SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&LAYER=vec&STYLE=default&TILEMATRIXSET=w&FORMAT=tiles&TILEMATRIX={z}&TILEROW={y}&TILECOL={x}&tk=',
      ).addTo(this.map);

        }
      }
    }
},
watch:{
}
}
