<template>
<!-- 统计报表 -->
  <div class="wrap">
    <el-row>
    <el-col :span="3">
    <div class="menu">
      <menus @data="getk"></menus>
    </div>
    </el-col>
    <el-col :span="21" :offset="3" style="top: 0;position: absolute;">
      <el-col :span="24">
        <searchs :sekey="sekey" @avdas="avdas" @sparams="sparams" @setcul="setcul"></searchs>
      </el-col>
       <!-- <tables v-if="sekey!=3&&sekey!=4" :data="data" :sekey="sekey"></tables> -->
       <!-- revise   @user:hz -->
      <div class="tableCon">
        <tables v-if="sekey==1" :data="data" :sekeys="sekey"></tables>
       
       <table2 v-if="sekey==2" :data="data" :sekeys="sekey"></table2>

       <dtables v-if="sekey==3" :data="data"></dtables>
       <alltables  v-if="sekey==4" :data="data" :sekey="sekey"></alltables>
      </div>
       
    </el-col>
  </el-row>
    <el-drawer
      :visible.sync="drawer"
      size="55%"
      :with-header="false">
      <el-row>
        <el-col :span="24">
        <div class="gjsx">
           <el-col :span="5">
                高级筛选
              </el-col>
              <el-col :span="2" :offset="17">
                <div @click="closehigh">
                    收起
                </div>
              </el-col>
        </div>
        </el-col>
      </el-row>
      <highs :sekey="sekey" @params="gparams"></highs>
    </el-drawer>
    <div class="pagination">
         <pagination @size="size" @current="current" :csize="csize" :cindex="cindex" :totalPage="totalPages"></pagination>
    </div>
    <el-dialog
      :visible.sync="isdeculm"
      width="30%"
      @close="close"
      >
      <span slot="title">
          <div class="otitle">自定义显示列项</div>
          <div class="subtitle">列项显示不得少于5项，最多支持自定义10个列项，灰色选中列不支持隐藏和排序</div>
      </span>
      <div>
        <colums @val="gval" :isSelects="isSelects"></colums>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="close">取 消</el-button>
        <el-button type="primary" @click="defined4">确 定</el-button>
      </span>
    </el-dialog>

  </div>
</template>
<script>
import menus from "./menus.vue"
import searchs from './searchs.vue';
import tables from "./tables.vue"
import table2 from "./table2.vue"
import dtables from "./dtables.vue"
import alltables from "./alltables.vue"
import highs from "./highs.vue"
import pagination from "./pagination.vue"
import colums from './colums'
import all from './all';
export default {
  components: {
    menus,
    searchs,
    tables,
    highs,
    pagination,
    dtables,
    colums,
    alltables,
    table2,
    
  },
  data() {
    return {
      isdeculm:false,
      sekey:1,
      drawer:false,
      user:"",
      region:"",
      cindex:1,
      csize:10,
      data:{},
      totalPages:0,
      params:{
        size:10,
        current:1,
        ejl:"",
        nd:2021,
        xzqdm:""
        },
      isproven:false,
      nowSelects:"",
      isSelects:false
    };
  },
  mounted(){
      // this.nowSelects=JSON.stringify(all.table)
      // localStorage.setItem("nowSelects",this.nowSelects)
      // this.getoneList()
      // this.isdeculm=true;
      // this.$nextTick(()=>{
      //     window.vm.$on('setcul', this.setcul);
      //     window.vm.$on('isxm', this.isxm);
      //  })
  },
  methods: {
    close(){
      this.isdeculm = false;
       window.vm.$emit("isSelects")
    },
    defined4(){
      this.isdeculm = false;
      localStorage.setItem("nowSelects",this.nowSelects)
       window.vm.$emit("isSelects")
    },
    gval(val){
      // console.log(val)
      this.nowSelects=JSON.stringify(val)
      this.isSelects=!this.isSelects
      
    },
    getoneList(){
       let params=this.params
        params.size=this.csize,
        params.current=this.cindex,
        // console.log("条件：",params)
        //  const loading = this.$loading({
        //   lock: true,
        //   text: 'Loading',
        //   spinner: 'el-icon-loading',
        //   background: 'rgba(0, 0, 0, 0.7)'
        // });
      xmjbxxtj(params).then(res=>{
         this.data=res
        this.totalPages=res.data.data.total
        window.vm.$emit('ttotal', this.totalPages);
           loading.close()
      }).catch(err=>{
            loading.close()
          })
    },
     gettwList(){
       let params=this.params
        params.size=this.csize,
        params.current=this.cindex,
        // console.log("条件：",params)
        //  const loading = this.$loading({
        //   lock: true,
        //   text: 'Loading',
        //   spinner: 'el-icon-loading',
        //   background: 'rgba(0, 0, 0, 0.7)'
        // });
      jctbxxtj(params).then(res=>{
         this.data=res
        this.totalPages=res.data.data.total
        window.vm.$emit('ttotal', this.totalPages);
           loading.close()
      }).catch(err=>{
            loading.close()
          })
    },
    getk(val){
      this.sekey=val.key
      if(this.sekey==1){
        this.getoneList()
        }
         if(this.sekey==2){
        this.gettwList()
        }
       if(this.sekey==3){
        this.getdata()
        }
       if(this.sekey==4){
       this.getdata4()
        } 
        // window.vm.$emit("sekey",val)
    },
    avdas(){
      this.drawer=true
    },
    // setcul(){
    //   this.drawer=true
    // },
    closehigh(){
      this.drawer=false
    },
    gparams(data){
      console.log(data)
      this.getdata()
    },
    sparams(data){
      console.log(data)
      if(data.isreset){
        this.csize=10
        this.cindex=1
        this.params.xzqdm=""
        this.params.ejl=""
        this.params.xmmc = ''
        window.vm.$emit('isreset');
      } else {
        this.params.xmmc = data.proName
          if(data.region1){
             if(data.region1=="全国"){
              this.params.xzqdm=""
            }else{
               this.params.xzqdm=data.region1
            }
           
          }
          if(data.region==""||val.region=="所有年份"){
            this.params.nd=""
          }else {
            this.params.nd=val.region
          }
          if(data.region2){
            console.log("全部类型",data.region2)
            if(data.region2=="全部类型"){
              this.params.ejl=""
            }else{
              this.params.ejl=data.region2.length > 1 ? data.region2[1] : ''
              this.params.yjl = data.region2[0]
            }
            
          }
      }
      
      // this.getdata()
       if(this.sekey==1){
        this.getoneList()
        }
         if(this.sekey==2){
        this.gettwList()
        }
       if(this.sekey==3){
        this.getdata()
        }
         if(this.sekey==4){
        this.getdata4()
        }
    },
    size(val){
      this.csize=val
      // this.getdata()
      if(this.sekey==1){
        this.getoneList()
        }
         if(this.sekey==2){
        this.gettwList()
        }
       if(this.sekey==3){
        this.getdata()
        }
         if(this.sekey==4){
        this.getdata4()
        }
    },
    current(val){
      this.cindex=val
      // this.getdata()
      if(this.sekey==1){
        this.getoneList()
        }
         if(this.sekey==2){
        this.gettwList()
        }
       if(this.sekey==3){
        this.getdata()
        }
         if(this.sekey==4){
        this.getdata4()
        }
    },
    getdata(){
      let params=this.params
        params.size=this.csize,
        params.current=this.cindex,
        // console.log("条件：",params)
        //  const loading = this.$loading({
        //   lock: true,
        //   text: 'Loading',
        //   spinner: 'el-icon-loading',
        //   background: 'rgba(0, 0, 0, 0.7)'
        // });
      getList(params).then(res=>{
        console.log("表格数据",res)
        this.data=res
        loading.close()
        this.totalPages=res.data.data.total
        window.vm.$emit('ttotal', this.totalPages);
      }).catch(err=>{
            loading.close()
          })
    },
    isxm(val){
      this.isproven=val
      // if(val){
      //   this.getdata()
      // }else{
      //   this.getdata2()
      // }
    },
    getdata2(){
      let params=this.params
        params.size=this.csize,
        params.current=this.cindex,
        // console.log("条件：",params)
      getasflist(params).then(res=>{
        console.log("表格数据",res)
        this.data=res
        this.totalPages=res.data.data.total
        window.vm.$emit('ttotal', this.totalPages);
      })
    },
    setcul(){
      this.isdeculm=true;
    },
    getdata4(){
       let params=this.params
        params.size=this.csize,
        params.current=this.cindex;
        // console.log("条件：",params)
        //   const loading = this.$loading({
        //   lock: true,
        //   text: 'Loading',
        //   spinner: 'el-icon-loading',
        //   background: 'rgba(0, 0, 0, 0.7)'
        // });
      zhsjtj(params).then(res=>{
        // console.log("表格数据444444",res)
        loading.close()

        this.data=res
        this.totalPages=res.data.data.total
        window.vm.$emit('ttotal', this.totalPages);
      }).catch(err=>{
            loading.close()
          })
    }
  },
  
};
</script>
<style lang="scss" scoped>
.wrap {
  height: 100vh;
  padding: 80px 0 0 0;
  text-align: left;
  box-sizing: border-box;
  overflow: hidden;
  // overflow-y: auto;
  // width:calc(100vw - 240px)
}
            
.gjsx{
  height: 80px;
  line-height: 80px;
  padding-left: 10px;
}
.pagination{
  position: absolute;
  bottom: 10px;
  right: 10px;
}
.subtitle{
  font-size: 12px;
  margin-top: 5px;
  color: #fff;
}
.otitle{
  font-size: 18px;
  color: #fff;
}
.tableCon{
  height: calc(100vh - 160px);
  padding-bottom: 20px;
  // overflow: auto;

}

</style>



