<template>
     <el-table
      :data="tableData"
      height="750"
      style="width: 100%">
      <el-table-column
        :prop="item.value"
        v-for="(item,index) in colums"
        :label="item.name"
        :key="index"
      
        width="180">
      </el-table-column>
     
    </el-table>
</template>

<script> 
// import { number } from "echarts/lib/export";
import data from "./onet"

export default {
    data () {
        return {
          sekey:1,
           tableData: [],
          colums:[],
         
        };
    },
     props:{
      data: {
            type: Object,
            default () {
                return {};
            }
        },
        sekeys:{
          type: Number,
            default () {
                return 1;
            }
        }
    },
    mounted () {
      this.sekey = this.sekeys
      console.log(this.sekey ,"setkeys变化")
      this.colums = data.one;
      // this.setkey(this.sekeys)
        if(this.data.data){
          this.tableData=this.data.data.data.records;
       }
      
      //   this.$nextTick(()=>{
      //     window.vm.$on('sekey', this.setkey);
      //  })

       
     
    },
    methods: {
      setkey(val){
        
        // this.sekey=val.key
        // if(this.sekey=="1"){
        //   this.colums=data.one
        // }
        // if(this.sekey=="2"){
        //   this.colums=data.two
        // }
        
      },
      
    },
    watch:{
      data:function(val){
         if(this.data.data){
          this.tableData=this.data.data.data.records;
        }
      },
      // sekey:function(val){
      //   console.log(val,'key')
      //   this.setkey(val)
      // }
    }
};
</script>

<style lang="scss" scoped>

</style>



