// import { CzmlDataSource } from "cesium";

/**
 * 加载 CZML 数据
 * @param {Object} viewer
 * @param {JSON} czml
 * @returns {Promise<unknown>}
 */
export default function (viewer, czml) {
  return new Promise((resolve, reject) => {
    try {
      // setTimeout(() => {
        let Source = window.Cesium.CzmlDataSource.load(czml);
        viewer.dataSources.add(Source).then(function (path) {
          // 更改时钟速率
          viewer.clock.multiplier = 50;
          resolve(path);
        });
      // }, 0);
    } catch (error) {
      reject(error);
    }
  });
}



