<template>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-sizes="[10, 20]"
          :page-size="100"
          class="paginations"
          layout="total, prev, pager, next, sizes"
          :total="totalPages">
        </el-pagination>
</template>

<script>

export default {
    data () {
        return {
           currentPage: 1,
           totalPages: 200
        };
    },
     props:{
      csize: {
            type: Number,
            default () {
                return 10;
            }
        },
      cindex: {
            type: Number,
            default () {
                return 1;
            }
        },
        totalPage:{
           type: Number,
            default () {
                return 0;
            }
        }
    },
    mounted () {
      this.totalPages=this.totalPage
       this.$nextTick(()=>{
         window.vm.$on('isreset', this.isreSet);
          window.vm.$on('ttotal', this.ttotal);
         
       })
    },
    methods: {
      handleSizeChange(val) {
        this.$emit("size",val)
      },
      handleCurrentChange(val) {
        this.$emit("current",val)
      },
      isreSet(val){
        this.currentPage=1
      },
      ttotal(val){
        this.totalPages=val
      }
    }
};
</script>

<style lang="scss">
.paginations .el-input__inner{
  height: 33px !important;
}
</style>



