<template>
<div>
  <el-form :inline="true"  class="high-form-inline" :model="form1" label-width="100px" v-if="sekey==1">
        <el-form-item label="年份:">
           <el-select v-model="form1.year" placeholder="所有年份">
            <el-option
              v-for="item in years"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="行政区划:">
          <el-select v-model="form1.region" placeholder="全国">
            <el-option
              v-for="item in divisions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
         <el-form-item label="项目类型:">
          <el-select v-model="form1.type" placeholder="全部类型">
          <el-option
              v-for="item in types"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
         <el-form-item label="形态类型:">
          <el-select v-model="form1.xtype" placeholder="全部类型">
            <el-option
              v-for="item in xztypes"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="项目名称:">
          <el-input v-model="form1.name" placeholder="项目类型"></el-input>
        </el-form-item>
        <el-form-item label="批准时间:">
           <el-date-picker
              v-model="form1.time"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
        </el-form-item>
          <div class="subbtns">
            <el-button type="primary" @click="search">查询</el-button>
            <el-button>重置</el-button>
          </div>
      </el-form>

      <el-form :inline="true"  class="high-form-inline" :model="form2" label-width="130px" v-if="sekey!=1">
        <el-form-item label="年份:">
           <el-select v-model="form2.year" placeholder="所有年份">
            <el-option
              v-for="item in years"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="行政区划:">
          <el-select v-model="form2.region" placeholder="全国">
            <el-option
              v-for="item in divisions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
         <el-form-item label="项目类型:">
          <el-select v-model="form2.type" placeholder="全部类型">
             <el-option
              v-for="item in types"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
         <el-form-item label="形态类型:">
          <el-select v-model="form2.xtype" placeholder="全部类型">
            <el-option
              v-for="item in xztypes"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        
        <el-form-item label="项目名称:">
          <el-input v-model="form2.name" placeholder="项目名称"></el-input>
        </el-form-item>
         <el-form-item label="边界分割类型:">
          <el-select v-model="form2.fgtype" placeholder="全部类型">
            <el-option
              v-for="item in fgtypes"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="建设时序类型:">
          <el-select v-model="form2.jstype" placeholder="全部类型">
            <el-option
              v-for="item in jstypes"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否偏移及改线:">
          <el-select v-model="form2.pytype" placeholder="请选择">
            <el-option
              v-for="item in pytypes"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="批准时间:">
           <el-date-picker
              v-model="form2.time"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
        </el-form-item>
        <el-form-item label="监测影像时间:">
           <el-date-picker
              v-model="form2.time2"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
        </el-form-item>
          <div class="subbtns">
            <el-button type="primary" @click="search">查询</el-button>
            <el-button @click="reset">重置</el-button>
          </div>
      </el-form>
</div>
  
      
</template>

<script>
export default {
    data () {
        return {
          region: '',
          user: '',
          value1: '',
          form1: {
            year: '所有年份',
            region: '全国',
            type: '全部类型',
            xtype: '全部类型',
            name: '',
            time: ''
          },
          form2: {
            year: '所有年份',
            region: '全国',
            type: '全部类型',
            xtype: '全部类型',
            name: '',
            time: '',
            time2: '',
            jstype: '全部类型',
            pytype: '全部',
            fgtype: '全部类型'
          },
          params:{},
          years:[
            {
              value: '所有年份',
              label: '所有年份'
            },
            {
              value: '2021',
              label: '2021'
            },
            {
              value: '2020',
              label: '2020'
            },
            {
              value: '2019',
              label: '2019'
            },
          ],
           divisions:[
            {
              value: '全国',
              label: '全国'
            },
            {
              value: '北京市',
              label: '北京市'
            },
            {
              value: '天津市',
              label: '天津市'
            }
          ],
           types:[
             {
              value: '全部类型',
              label: '全部类型'
            },
            {
              value: '机场',
              label: '机场'
            },
            {
              value: '铁路',
              label: '铁路'
            },
          ],
           xztypes:[
             {
              value: '全部类型',
              label: '全部类型'
            },
            {
              value: '块状',
              label: '块状'
            },
            {
              value: '线性',
              label: '线性'
            },
          ],
           fgtypes:[
             {
              value: '全部类型',
              label: '全部类型'
            },
            {
              value: '界内开发建设图斑',
              label: '界内开发建设图斑'
            },
            {
              value: '界内未开发建设图斑',
              label: '界内未开发建设图斑'
            },
          ],
          jstypes:[
            {
              value: '全部类型',
              label: '全部类型'
            },
            {
              value: '主体工程本底开发建设图斑',
              label: '主体工程本底开发建设图斑'
            },
            {
              value: '暂不需审批建设类图斑',
              label: '暂不需审批建设类图斑'
            },
          ],
          pytypes:[
            {
              value: '全部',
              label: '全部'
            },
            {
              value: '是',
              label: '是'
            },
            {
              value: '否',
              label: '否'
            }
          ]
        };
    },
    props: ['sekey'],
    mounted () {
    },
    methods: {
      search(){
        if(this.sekey==1){
          this.params=this.form1
        }else{
          this.params=this.form2
        }
        
        this.$emit("params",{data:this.params})
      },
      reset(){
        
        this.form1.year='所有年份'
        this.form1.region='全国'
         this.form1.type='全部类型'
        this.form1.xtype= '全部类型'
         this.form1.name= ''
            this.form1.time= ''


          this.form2.year= '所有年份'
           this.form2.region= '全国'
            this.form2.type= '全部类型'
            this.form2.xtype= '全部类型'
            this.form2.name= ''
            this.form2.time= ''
            this.form2.time2= ''
            this.form2.jstype= '全部类型'
            this.form2.pytype= '全部'
            this.form2.fgtype= '全部类型'
      }
    }
};
</script>

<style lang="scss" scoped>
  /deep/.el-form--inline .el-form-item{
    width: 45%;
  }
  /deep/.el-form--inline .el-form-item__content{
    width: 200px;
  }
  .subbtns{
    padding: 10px;
  }
</style>



