<template>
  <div class="loginWrap">
    <div>
      <div class="login-tit">
        <div class="logo"></div>
        <div class="txt">
          某某分析系统
          <p class="sub">
          xxxxxx  ssss   ssss  moumou projects
          </p>
        </div>
      </div>
      <div class="loginBox">
        <el-form class="loginForm"
                 :model="formData">
          <el-form-item label="">
            <el-input v-model="formData.username"
                      placeholder="请输入您的用户名"><i slot="prefix"
                 class="el-input__icon el-icon-s-custom"></i></el-input>
          </el-form-item>
          <el-form-item>
            <el-input type="password"
                      v-model="formData.password"
                      placeholder="请输入您的密码"><i slot="prefix"
                 class="el-input__icon el-icon-lock"></i></el-input>
          </el-form-item>
          <el-form-item>
            <el-checkbox v-model="formData.remember">记住密码</el-checkbox>
          </el-form-item>
          <el-form-item>
            <el-button type="primary"
                       @click="submitLogin">登录</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
// import { mapActions } from 'vuex';

export default {
  data () {
    return {
      formData: {
        tenantId: '000000',
        username: '',
        password: '',
        remember: false
      }
    };
  },
  methods: {
    submitLogin () {
          this.$router.push('/indexh');
    }
  }
};
</script>
<style lang="scss" scoped>
.loginWrap {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background: url("../../assets/images/bg.png") no-repeat center top;
  background-size: 100% 100%;
  .login-tit {
    font-size: 46px;
    font-family: "PingFangSC";
    font-weight: bold;
    color: #fff;
    letter-spacing: 2px;
    .logo {
      display: inline-block;
      width: 70px;
      height: 78px;
      background: url("../../assets/images/logo.png") no-repeat center left;
      background-size: contain;
      vertical-align: top;
    }
    .txt {
      display: inline-block;
      padding-top: 5px;
      vertical-align: top;
    }
    .sub {
      margin-top: 10px;
      font-size: 18px;
      font-weight: normal;
      color: rgba($color: #fff, $alpha: 0.7);
    }
  }
  .loginBox {
    width: 575px;
    height: 517px;
    margin: 130px auto 0;
    padding: 150px 123px 0;
    background: url("../../assets/images/login.png") no-repeat center top;
    background-size: 100% 100%;
    box-sizing: border-box;
  }
  .loginForm {
    margin: auto;
    text-align: left;
    /deep/ .el-form-item__label {
      line-height: 40px;
      margin-bottom: 24px;
      font-family: "PingFangSC";
      font-size: 24px;
      color: #fff;
    }
    .el-input {
      border: 1px solid #49deff;
    }
    /deep/ .el-input__inner {
      width: calc(100% - 35px);
      height: 40px;
      line-height: 40px;
      margin-left: 35px;
      padding-left: 15px;
      font-size: 15px;
      color: #fff;
      background: rgba(73, 222, 255, 0.38);
      border: none;
      border-radius: 0;
    }
    /deep/ .el-button {
      width: 100%;
      height: 48px;
      color: #fff;
      font-family: "PingFangSC";
      font-size: 26px;
      border-radius: 0;
      border: none;
      letter-spacing: 5px;
      &.el-button--primary {
        background: linear-gradient(90.77deg, #2596ff 0%, #00fff0 100%);
      }
    }
    /deep/ .el-input__icon {
      font-size: 16px;
      color: #fff;
    }
    /deep/ .el-checkbox__label {
      font-size: 16px;
      color: #fff;
    }
    /deep/ .el-checkbox__inner {
      width: 20px;
      height: 20px;
      background-color: transparent;
      border-color: #49deff;
    }
    /deep/ .el-checkbox__inner::after {
      width: 5px;
      height: 10px;
      left: 6px;
      border-color: #49deff;
    }
  }
}
</style>



